var { i18njs } = require('../method/i18njs');

/**
 * @module _msg
 * @param {String} msgKey 
 * @param  {...String} params wikitext
 */
var _msg = async function (msgKey, ...params) {
  var i18n = i18njs.loadMessages('InPageEdit', {}, require('../i18n/languages.json'));
  return i18n.msg(msgKey, ...params).parse();
}

module.exports = {
  _msg
}